<template>
  <Masthead />
  <div id="scene-container">
    <div id="movie-detail">
      <div class="main-content">
        <div class="mc-l">
          <div class="player-wrap">
            <div class="embed-responsive">
              <div class="player-module">
                <div class="player-container">
                  <DPlayer
                    v-if="videoSource"
                    :source="videoSource"
                    :progress="last_watch_progress"
                    @playEnded="playNextEpisode"
                    @onProgress="saveWatchProgress"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="media-warp">
            <div class="movie-intro">
              <div :class="['intro-wrap', isCollapse && 'collapse']">
                <div class="intro-detail">
                  {{ movie?.introduction }}
                  <span class="down-up up-box" @click="toggleCollapse"
                    >收起
                    <i class="el-icon-arrow-up"></i>
                  </span>
                </div>
                <span class="down-up down-box" @click="toggleCollapse"
                  >展开
                  <i class="el-icon-arrow-down"></i>
                </span>
              </div>
            </div>
            <MovieToolbar v-if="movie" :movie="movie" />
            <div v-if="movie?.play_lines" class="module-wrap show-lg">
              <MovieEplist
                :playlines="movie?.play_lines"
                :series="series"
                :current-episode="videoSource"
                @changePlaylines="changePlaylines"
                @changeEpisode="changeEpisode"
              />
            </div>
            <div class="related-movies show-lg">
              <div class="section-title">
                <h3>
                  影视推荐
                </h3>
              </div>
              <div class="movie-items">
                <Skeleton v-if="!relatedMovies?.length" type="recommend-movies" :count="4" />
                <router-link
                  v-else
                  class="item-wrap"
                  v-for="item in relatedMovies.slice(0, 4)"
                  :key="item.id"
                  :to="`/movie/${item.id}`"
                >
                  <RCMovieItem :movie="item" />
                </router-link>
              </div>
            </div>
            <CommentModule v-if="movie?.id" :commentable-id="movie?.id" />
          </div>
        </div>
        <div class="mc-r">
          <div class="module-wrap">
            <MovieInfo :movie="movie" />
          </div>
          <div v-if="movie?.play_lines" class="module-wrap">
            <MovieEplist
              :playlines="movie?.play_lines"
              :series="series"
              :current-episode="videoSource"
              @changePlaylines="changePlaylines"
              @changeEpisode="changeEpisode"
            />
          </div>
          <div class="related-movies">
            <div class="section-title">
              <h3>
                影视推荐
              </h3>
            </div>
            <div class="movie-items">
              <Skeleton v-if="!relatedMovies?.length" type="recommend-movies" />
              <router-link
                v-else
                class="item-wrap"
                v-for="movie in relatedMovies"
                :key="movie?.id"
                :to="`/movie/${movie?.id}`"
              >
                <RCMovieItem :movie="movie" />
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Footer />
  <SideBar />
</template>

<script lang="ts">
import { ref, watch } from 'vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { useApolloClient } from '@vue/apollo-composable';
import { throttle } from 'lodash';
import {
  useMovieQuery,
  useRelatedMoviesQuery,
  SAVE_WATCH_PROGRESS_MUTATION,
  FAVORITE_MOVIES_QUERY,
  MOVIES_HISTORY_QUERY
} from '@/graphql';
import DPlayer from '@/components/player/DPlayer.vue';
import CommentModule from '@/components/comment/CommentModule.vue';
import RCMovieItem from '@/components/RCMovieItem.vue';
import Skeleton from '@/components/Skeleton.vue';
import MovieInfo from './parts/MovieInfo.vue';
import MovieToolbar from './parts/MovieToolbar.vue';
import MovieEplist from './parts/MovieEplist.vue';

export default {
  name: 'MovieDetail',
  components: {
    DPlayer,
    CommentModule,
    RCMovieItem,
    Skeleton,
    MovieInfo,
    MovieToolbar,
    MovieEplist
  },
  setup(props) {
    const route = useRoute();
    const movieId = ref(Number(route.params?.id));
    watch(route, (value, prevValue) => {
      movieId.value = Number(value.params?.id);
    });
    const { movie } = useMovieQuery({
      id: movieId
    });
    const { relatedMovies } = useRelatedMoviesQuery({
      movie_id: movieId,
      limit: 12
    });

    const play_lines = ref(movie.value?.play_lines);
    const last_watch_series = ref(movie.value?.last_watch_series || 0);
    const last_watch_progress = ref(movie.value?.last_watch_progress || 0);
    const series = ref(play_lines.value?.[0]?.data);
    const videoSource = ref(series.value?.[last_watch_series.value]);
    watch(movie, (value, prevValue) => {
      play_lines.value = value?.play_lines;
      last_watch_series.value = value?.last_watch_series || 0;
      last_watch_progress.value = value?.last_watch_progress || 0;
      series.value = play_lines.value?.[0]?.data;
      videoSource.value = series.value?.[last_watch_series.value];
    });

    // 选集
    function changeEpisode({ episode, episodeIndex }) {
      if (episodeIndex >= 0) {
        last_watch_series.value = episodeIndex;
      } else {
        last_watch_series.value = series.value.indexOf(videoSource.value);
      }
      videoSource.value = episode;
    }
    // 播放下一集
    function playNextEpisode() {
      last_watch_series.value++;
      videoSource.value = series.value?.[last_watch_series.value];
    }

    // 切换线路
    function changePlaylines(data) {
      series.value = data;
      videoSource.value = series.value?.[last_watch_series.value];
    }

    const client = useApolloClient().client;
    const store = useStore();
    const me = ref(store.state?.user?.user);
    async function saveProgressMutation(progress: Number) {
      if (me.value?.id) {
        await client.mutate({
          mutation: SAVE_WATCH_PROGRESS_MUTATION,
          variables: {
            movie_id: movieId.value,
            series_index: last_watch_series.value,
            progress
          },
          refetchQueries: () => [
            {
              query: FAVORITE_MOVIES_QUERY,
              variables: { user_id: me.value?.id }
            },
            {
              query: MOVIES_HISTORY_QUERY
            }
          ]
        });
      }
    }
    const saveWatchProgress = throttle(saveProgressMutation, 8000);

    return {
      movie,
      relatedMovies,
      play_lines,
      series,
      videoSource,
      last_watch_progress,
      changeEpisode,
      playNextEpisode,
      changePlaylines,
      saveWatchProgress
    };
  },
  methods: {
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    }
  },
  data() {
    return {
      isCollapse: true
    };
  }
};
</script>

