<template>
  <div v-if="playlines?.length > 1 || series?.length > 1">
    <div class="movie-eplist">
      <div class="list-title">
        <h4 title="选集 | 线路">
          {{ series?.length > 1 ? '选集' : '线路' }}
          <span v-if="series?.length > 10" @click="drawer = true" class="more show-lg">
            共{{ series?.length }}集
            <i class="el-icon-arrow-right"></i>
          </span>
        </h4>
        <el-dropdown v-if="series?.length > 1 && playlines?.length > 1" @command="changePlaylines">
          <div class="change-line el-dropdown-link">
            线路
            <i class="el-icon-arrow-down el-icon--right"></i>
          </div>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item
                v-for="(item, index) in playlines"
                :key="index"
                :command="index"
                :disabled="playlineIndex ? playlineIndex === index : index === 0"
              >
                <span>
                  {{ item.name }}
                </span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
      <div v-if="series?.length > 1" class="list-wrapper longlist">
        <ul class="ep-list">
          <li
            v-for="(episode, index) in series"
            :key="index"
            :class="['ep-item', currentEpisode.url === episode.url && 'visited']"
          >
            <a
              @click="changeEpisode(episode, index)"
              :title="episode.name || `第${index + 1}集`"
              class="ep-button  text-ellipsis"
              href="javascript:void(0)"
            >
              <span class="ep-title">第{{ index + 1 }}集</span>
            </a>
          </li>
        </ul>
      </div>
      <div v-else class="list-wrapper">
        <ul class="playlines">
          <li
            v-for="(item, index) in playlines"
            :key="index"
            :class="{
              'ep-item': true,
              'text-ellipsis': true,
              visited: playlineIndex ? playlineIndex === index : index === 0
            }"
          >
            <a
              @click="changePlaylines(index)"
              :title="item.name + ' · ' + item.data[0].name"
              class="ep-button text-ellipsis"
              href="javascript:void(0)"
            >
              <span class="ep-title">{{ item.name }} · {{ item.data[0].name }}</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <el-drawer
      v-if="series?.length > 10"
      v-model="drawer"
      :title="`选集(${series?.length})`"
      direction="btt"
      size="60%"
      custom-class="drawer_ep-list"
    >
      <ul class="ep-list show-lg">
        <li
          v-for="(episode, index) in series"
          :key="index"
          :class="['ep-item', currentEpisode.url === episode.url && 'visited']"
        >
          <a
            @click="changeEpisode(episode, index)"
            :title="episode.name || `第${index + 1}集`"
            class="ep-button  text-ellipsis"
            href="javascript:void(0)"
          >
            <span class="ep-title">第{{ index + 1 }}集</span>
          </a>
        </li>
      </ul>
    </el-drawer>
  </div>
</template>

<script>
export default {
  props: {
    series: Array,
    playlines: Array,
    currentEpisode: Object
  },
  methods: {
    changeEpisode(episode, episodeIndex) {
      this.$emit('changeEpisode', { episode, episodeIndex });
    },
    changePlaylines(playlineIndex) {
      this.playlineIndex = playlineIndex;
      this.$emit('changePlaylines', this.playlines[playlineIndex].data);
    }
  },
  data() {
    return {
      playlineIndex: '',
      drawer: false
    };
  }
};
</script>

