<script lang="ts">
import { ref, toRefs } from 'vue';
import { debounce } from 'lodash';
import { ElMessage } from 'element-plus';
import useClipboard from 'vue-clipboard3';
import { useFavoriteMutation } from '@/graphql';

export default {
  props: {
    movie: Object
  },

  setup(props) {
    const { movie } = toRefs(props);
    const favorited = ref(movie.value?.favorited);
    const link = movie.value?.id
      ? `#${movie.value?.name ?? movie.value?.introduction}#，精彩视频，邀您来观看！` +
        `https://juhaokantv.com/movie/${movie.value?.id}`
      : '';
    const movieLink = ref(link);

    const { toggleFavorite, onDone } = useFavoriteMutation(
      { id: movie.value.id, type: 'movies' },
      { fetchPolicy: 'network-only' }
    );

    onDone(({ data }) => {
      if (data) {
        favorited.value = data?.toggleFavorite?.favorited;
        ElMessage({
          message: `${data?.toggleFavorite?.favorited ? '已追剧!' : '取消追剧！'}`,
          type: 'success'
        });
      }
    });

    const favoriteHandler = debounce(() => {
      favorited.value = !favorited.value;
      toggleFavorite();
    }, 500);

    const { toClipboard } = useClipboard();
    async function copyMovieLink() {
      if (movieLink.value) {
        try {
          await toClipboard(movieLink.value);
          ElMessage({ message: '复制成功', type: 'success' });
        } catch (error) {
          ElMessage({ message: '复制失败', type: 'error' });
        }
      } else {
        ElMessage({ message: '链接出错', type: 'error' });
      }
    }

    function watchTogether() {
      ElMessage({
        message: '即将上线，敬请期待',
        type: 'info'
      });
    }

    return {
      movieLink,
      favorited,
      favoriteHandler,
      copyMovieLink,
      watchTogether
    };
  },
  methods: {
    showReportModal() {
      this.$bus.emit('SHOW_REPORT_MODAL', { id: this.movie.id, title: '举报影视' });
    },
    downloadApp() {
      (window as any).ULink([
        {
          id: 'usr1kj7mbfbqffk5', // 后台生成的裂变活动LinkID
          data: {
            // 传递的自定义动态参数
            movie_id: this.movie.id,
            route: 'MovieDetail'
          },
          // selector: '#xxl', //按钮的名称
          // 可选高级功能，具体含义请看下方U-Link API文档
          auto: true,
          timeout: 2000,
          lazy: false
        }
      ]);
    }
  }
};
</script>

